export default {
    data() {
        return {
            inviteCode: '----'
        }
    },
    methods: {
        initShare() {
            const shareTypes = [
                'onMenuShareTimeline',
                'onMenuShareAppMessage',
                'onMenuShareQQ',
                'onMenuShareWeibo',
                'onMenuShareQZone'
            ]
            const data = {
                title: APP_CONFIG.share.title, // 分享标题
                desc: APP_CONFIG.share.desc, // 分享描述
                link: `http://bike.yunba.io/${APP_CONFIG.id}/invite?code=${this.inviteCode}`,
                imgUrl: '',
                success: () => {
                    this.$vux.toast.show({
                        text: '分享成功'
                    });
                }
            };
            window.WECHAT_READY.then(() => {
                shareTypes.forEach((item) => {
                    wx[item](data);
                });
            });
        }
    },
    created() {
        // 获取邀请码
        this.$http({
            url: `${BASE_URL}/lock-account/invite_code`
        }).then((result) => {
            const data = result.data;
            if (data.status == 0) {
                this.inviteCode = data.code;
                this.initShare();
            } else {
                this.$vux.toast.show({
                    type: 'warn',
                    text: '获取数据失败'
                })
            }
        });
    }
}



